import React from "react";
import PropTypes from "prop-types";
import {withStyles} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Home from "@material-ui/icons/HomeRounded";
import Input from "@material-ui/core/Input";

const styles = () => ({
  root: {
    flexGrow: 1,
    marginLeft: 50,
    backgroundPosition: 0,
    display: "flex",
    flexDirection: "column",
    padding: "2rem"
  },
  title: {
    fontSize: "1.5rem",
    textAlign: "center",
    margin: "2rem 0"
  },
  flex: {
    display: "flex"
  },
  input: {
    borderRadius: 5,
    border: "1px solid rgba(112, 112, 112, 0.16)",
    background: "rgba(233, 231, 231, 0.26)",
    boxShadow: "0 3px 6px rgba(0, 0, 0, 0.16)",
    padding: "0.25rem 1.5rem",
    "&::before": {
      border: 0
    },
    "&:hover::before": {
      border: 0
    }
  },
  ipGrid: {
    marginTop: "0.5rem"
  },
  wrapper: {
    maxWidth: 1000,
    margin: "auto",
    marginTop: 0
  },
  height: {
    height: 300
  }
});

function Contact(props) {
  const {classes} = props;
  return (
    <div className={classes.root}>
      <Typography variant="button" color="primary" className={classes.title}>contact me</Typography>
      <div className={classes.wrapper}>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={4} lg={4}>
            <Paper className={classes.flex}>
              <div>
                <Home />
              </div>
              <div>
                this will be the address
              </div>
            </Paper>
          </Grid>
          <Grid item xs={12} sm={4} lg={4}>
            <Paper className={classes.flex}>
              <div>
                <Home />
              </div>
              <div>
                this will be the address
              </div>
            </Paper>
          </Grid>
          <Grid item xs={12} sm={4} lg={4}>
            <Paper className={classes.flex}>
              <div>
                <Home />
              </div>
              <div>
                this will be another one
              </div>
            </Paper>
          </Grid>
        </Grid>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6} lg={6} className={classes.ipGrid}>
            <Input
              fullWidth
              className={classes.input}
              placeholder="Your Name"
            />
          </Grid>
          <Grid item xs={12} sm={6} lg={6} className={classes.ipGrid}>
            <Input
              fullWidth
              className={classes.input}
              placeholder="Your Email ID"
            />
          </Grid>
          <Grid item xs={12} sm={12} lg={12} className={classes.ipGrid}>
            <Input
              fullWidth
              classes={{input: classes.height}}
              multiline
              className={classes.input}
              placeholder="Write your message here..."
            />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}

Contact.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Contact);
